<template>
	<view class="dj-user">
		<view class="dj-user">
			<view class="user-job">
				主播
			</view>
			 <view class="user-row">
				<view class="user-avatar">
				 	<image :src="avatarUrl" mode=""></image>
				</view>
				<view class="user-info">
					<view class="user-name">
						{{nickname}}
					</view>
					<view class="user-desc">
						{{name}}
					</view>
				</view>
				<view class="appreciate">
					<view class="appreciate-box">
						<text>赞赏</text>
					</view>
				</view>
			 </view> 
		</view>
		<view class="dj-info">
			<text class="dj-title">电台内容简介</text>
			<view class="dj-category">
				分类：<text>{{category}}</text>
			</view>
			<view class="dj-desc">
				<text>{{desc}}</text>
			</view>
		</view>
		<view class="commentDatas">
			<view class="comment-title">
				精彩评论
			</view>
			<view class="comment-content">
				<block v-for="(item,index) in commentDatas" :key='index'>
					<view class="comment-item">
						<view class="item-img">
							<image :src="item.userProfile.avatarUrl"></image>
						</view>
						<view class="item-info">
							<text class="nick-name">{{item.userProfile.nickname}}</text>
							<view class="comment-desc">{{item.content}}</view>
							<text class="programName">——{{item.programName}}</text>
						</view>
					</view>
				</block>
			</view>
		</view>
	</view>
</template>

<script>
	export default{
		props:{
			avatarUrl:{
				type:String
			},
			nickname:{
				type:String
			},
			category:{
				type:String
			},
			desc:{
				type:String
			},
			name:{
				type:String
			},
			commentDatas:{
				type:Array
			}
		}
	}
</script>

<style>
	.user-job{
		width: 100%;
		height: 60rpx;
		line-height: 60rpx;
		margin-left: 20rpx;
		font-weight: 600;
	}
	.user-row{
		width: 100%;
		height: 100rpx;
		display: flex;
	}
	.user-avatar{
		width: 100rpx;
		height: 100rpx;
		margin-left: 20rpx;
	}
	.user-avatar image{
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	.user-info{
		flex: 1;
		margin-left: 20rpx;
	}
	.user-name{
		font-size: 34rpx;
	}
	.user-desc{
		font-size: 24rpx;
		color: #ccc;
	}
	.appreciate{
		width: 150rpx;
		height: 100rpx;
		padding-right: 20rpx;
		/* background-color: red; */
	}
	.appreciate-box{
		width: 130rpx;
		height: 45rpx;
		border-radius: 50rpx;
		margin-top: 20rpx;
		text-align: center;
		border: 2rpx solid red;
		color: red;
	}
	.appreciate-box text{
		font-size: 24rpx;
	}
	.dj-info{
		width: 100%;
		margin-top: 30rpx;
		padding-left: 20rpx;
		padding-bottom: 30rpx;
		box-sizing: border-box;
		/* background-color: red; */
	}
	.dj-title{
		font-weight: 600;
	}
	.dj-category{
		font-size: 28rpx;
		color: #ccc;
		margin-top: 20rpx;
	}
	.dj-category text{
		font-size: 20rpx;
		padding: 0 8rpx;
		color: red;
		border: 1rpx solid red;
		border-radius: 8rpx;
	}
	.dj-desc{
		margin-top: 20rpx;
	}
	.dj-desc text{
		font-size: 28rpx;
		color: #ccc;
	}
	.commentDatas{
		width: 100%;
		padding-left: 20rpx;
		box-sizing: border-box;
		/* background-color: red; */
	}
	.comment-title{
		width: 100%;
		height: 80rpx;
		line-height: 80rpx;
	}
	.comment-content{
		width: 100%;
		/* background-color: pink; */
	}
	.comment-item{
		width: 100%;
		margin-bottom: 30rpx;
		padding-bottom: 10rpx;
		display: flex;
		/* background-color: green; */
	}
	.item-img{
		width: 90rpx;
		height: 90rpx;
	}
	.item-img image{
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	.item-info{
		width: 600rpx;
		margin-left: 20rpx;
		border-bottom: 2rpx solid #ccc;
	}
	.nick-name{
		font-size: 26rpx; 
		color: #ccc;
	}
	.comment-desc{
		font-size: 28rpx;
		margin: 20rpx 0;
		/* background-color: #007AFF; */
	}
	.programName{
		font-size: 20rpx;
		color: #ccc;
	}
</style>
